body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  color: white;
}

.blocks {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.block {
  flex: 0 0 100%;
  height: 300px;
  text-align: center;
  line-height: 300px;
}

.block-1 {
  background-color: rgb(255 0 0);
  order: 1;
}

.block-2 {
  background-color: rgb(204 0 0);
  order: 2;
}

.block-3 {
  background-color: rgb(153 0 0);
  order: 3;
}

.block-4 {
  background-color: rgb(102 0 0);
  order: 4;
}

.block-5 {
  background-color: rgb(51 0 0);
  order: 5;
}

.block-6 {
  background-color: rgb(0 0 0);
  order: 6;
}

@media (min-width: 900px) {
  .block {
    flex-basis: 33.33%;
  }

  .block-4 {
    order: 6;
  }

  .block-6 {
    order: 4;
  }
}

@media (min-width: 600px) and (max-width: 899px) {
  .block {
    flex-basis: 50%;
  }

  .block-3 {
    order: 4;
  }

  .block-4 {
    order: 3;
  }
}
